export const state = {
  // 购物车商品列表
  shopInfo: [],
  // 购物车商品总个数
  sumNum: 0,
  // 购物车商品总价
  sumPrice: 0,
  // 显示购物车列表
  shopCatBool: false,
  // 显示加入购物车按钮
  cartBtnBool: false,
  // 0:显示全部评论， 1：显示满意评论，2：显示不满意评论
  checkState: 0,
  // 是否有内容
  hasContext: false
}



